html, body, #root {
  height: 100%;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  background-color: rgba(19, 46, 50, 1);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.main-title {
  color: white;
  margin: 24px;
}

.actions {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.Button {
  background: transparent;
  border-radius: 2px;
  border: 1px solid rgba(29, 132, 181, 1);
  color: rgba(29, 132, 181, 1);
  font-size: 2em;
  margin: 24px;
  padding-left: 16px;
  padding-right: 16px;
  text-transform: uppercase;
}

.Button:hover {
  background:rgba(0, 0, 0, 0.5);
}

.Button.clear {
  border-color: rgba(255, 77, 30, 1);
  color: rgba(255, 77, 30, 1);
}

.timer-info {
  color: white;
  font-size: 5em;
  margin: 24px;
}

